<script>
import { onMounted, watch } from "vue";
import { useBornEmbryoTransferInit } from "@/composable/born";
export default {
    setup() {
        const {
            store,
            form,
            v$,
            fillForm,
            reasonsNoETOptions,
            preparationUterineLiningOptions,
            assistedZonaHatchingOptions,
            sourceTransferredEmbryosOptions,
            transferInseminationMethodOptions,
            charTransferredEmbryoOptions,
            numberThawsOptions,
            sitePGTembryoOptions,
            save
        } = useBornEmbryoTransferInit();

        watch(() => store.state.born.embryoTransfer, fillForm);

        onMounted(() => {
            if (store.state.born.embryoTransfer == null) {
                store.dispatch(
                    "born/getBronV2",
                    store.state.artCycle.selectedCycle
                );
            } else {
                fillForm(store.state.born.embryoTransfer);
            }
        });

        return {
            form,
            v$,
            save,
            reasonsNoETOptions,
            preparationUterineLiningOptions,
            assistedZonaHatchingOptions,
            sourceTransferredEmbryosOptions,
            transferInseminationMethodOptions,
            charTransferredEmbryoOptions,
            numberThawsOptions,
            sitePGTembryoOptions
        };
    }
};
</script>

